var _self;
var isLoadInterFace = false;
export default {
	data() {
		return {
			listIsFree:false,
			storeId:0,
			currentPage:1,
			totalPage:1,
			isLast:true,
			dataList:[]
		}
	},
	onLoad(opt) {
		_self=this;
		if(opt.id != undefined){
			console.log(opt.id);
			_self.storeId = opt.id;
			_self.getDataList(1);
		}
	},
	onReachBottom() {
		this.nextPage();
	},
	methods: {
		//加载下一页
		nextPage(){
			console.log(_self.isLast);
			if(_self.isLast){
				uni.showToast({
					title: '没有更多了',
					icon:"none"
				});
			}else{
				if(!isLoadInterFace){
					isLoadInterFace = true;
					var pageNum = _self.currentPage + 1;
					_self.getDataList(pageNum);
				}
			}
		},
		getDataList(pageNum){
			var dataObj = {
					storeid:_self.storeId,
					limit:10,
					page:pageNum
				};
			
			console.log(dataObj);
			uni.request({
			    url: _self.$httpUrl+'index.php/api/store/couponinfo', 
				method:"POST",
				data:dataObj,
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					_self.dataInfo = res.data.result;
					_self.dataList = _self.dataList.concat(res.data.result.data);
					_self.currentPage = res.data.result.current_page;
					_self.isLast = _self.currentPage >= res.data.result.last_page ? true : false;
				  }else{
					  uni.showToast({
					  	icon:"none",
					  	title:res.data.msg
					  })
				  }
				  
				  
				  if(_self.dataList.length <= 0){
				  	_self.listIsFree = true;
				  }else{
				  	_self.listIsFree = false;
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					isLoadInterFace = false;
			    }
			});
		}
	}
}

